.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 98%;
    button {
        height: auto;
        min-height: 40px;
        margin-top: 12px;
        padding: 2px 8px;
        color: #37383c;
        font-size: 14px;
        border-radius: 4px;
        border: 1px solid #e6e9ee;
        background-color: #f4f6f7;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 20px 40px 20px;
        &:hover {
            color: #2587e8;
            background-color: #ebf4fd;
        }
    }
    .result {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
        overflow: hidden;
        padding: 20px 28px 10px 28px;
        .message {
        }
        .answer {
            margin-top: 20px;
        }
    }
}
